<template>
  <div class="hs-container">
    <c-header :title="$t('base.OrderDetails')" isBackNav fixedBackPath="/train/orderInfoList"></c-header>
    <hs-scroll
      v-if="details && details.Routes"
      class="table-view"
      :data="[details]"
      :options="tableViewOptions"
      @pulling-down="_getOrderDetails">
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="cell-top">
              <div class="cell-top-one" :type="language">
                <p>{{details.OrderStatusDesc}}</p>
                <p>{{details.VettingStatusDesc}}</p>
                <p>{{details.OrderStatusDesc}}</p>
              </div>
              <div class="cell-top-two">
                <p>{{$t('base.OrderNumber')}}: {{details.OrderNo}}</p>
                <p>{{$t('base.OrderDate')}}: {{$t(beTime(details.CreateDate), 'date', ['yyyy-MM-dd', 'MM dd, yyyy'])}}&nbsp;{{beTime(details.CreateDate, 'hh:mm')}}</p>
              </div>
              <div  class="cell-button" v-if="$route.query.isOwner">
                <hs-button v-if="details.PageBtnInfo.IsAllowPay" @click="_gotoPay"
                  class="button">{{$t('base.Pay')}}</hs-button>
                <hs-button v-if="details.PageBtnInfo.IsAllowCancel" @click="_getCancle"
                  class="button" type="common">{{$t('base.CancelOrder')}}</hs-button>
              </div>
                <div class="cell-button" v-if="infoIndex >= 0 && $route.query.isExamine && Number(details.VettingStatus) === 1">
                <hs-button class="button" @click="[vettingContent = 1, showExamineDialog = true]">{{$t('base.Pass')}}</hs-button>
                <hs-button class="button" type="common" @click="[vettingContent = 0, showExamineDialog = true]">{{$t('base.Reject')}}</hs-button>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <train-segment v-if="!this.noValue(JSON.stringify(origin))" :detail="origin" :isOrigin="1+''">
          <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(ticket, ticketIndex) in origin.Tickets" :key="ticketIndex">
            <!-- :class="{'custom-line': ticketIndex === 0}" -->
            <div class="custom-cell-people">
              <div class="left" :style="{opacity: ticketIndex === 0 ? 1 : 0}">
                <p>{{$t('base.Passenger')}}</p>
              </div>
              <div class="right" :style="{'border-bottom-width': ticketIndex === origin.Tickets.length - 1 ? '0px' : '1px'}">
                <div class="name">
                  <p>{{ticket.PsgName}}</p>
                  <span>{{ticket.IssueStatus | ticketStatus}}</span>
                </div>
                <div class="others">
                  <p v-if="ticket.Credential">{{ticket.Credential.CredentialName}} {{ticket.Credential.CredentialNo}}</p>
                  <P>{{$t('base.Seat')}} {{ticket.SeatTypeName}} {{ticket.SeatPosition}}</P>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </train-segment>
        <div v-for="(item, index) in details.Routes" :key="index">
          <train-segment :detail="item" :isOrigin="JSON.stringify(origin) !== '{}' ? (0+'') : (0+'')" :mark="_showSegmentTitle(index)">
            <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(ticket, ticketIndex) in item.Tickets" :key="ticketIndex">
              <div class="custom-cell-people" :class="{'custom-line': ticketIndex === 0 && (details.OrderStatus === 5 || details.OrderStatus === 10 || details.OrderStatus === 11 || details.OrderStatus === 13 || details.OrderStatus === 14) && (((new Date()).valueOf() + 1000 * 60 * 60 * 2) < item.DepartDate * 1000)}">
                <div class="left" :style="{opacity: ticketIndex === 0 ? 1 : 0}">
                  <p>{{$t('base.Passenger')}}</p>
                </div>
                  <div class="right" :style="{'border-bottom-width': ticketIndex === item.Tickets.length - 1 ? '0px' : '1px'}">
                    <div class="name">
                      <p>{{ticket.PsgName}}</p>
                      <span>{{ticket.IssueStatus | ticketStatus}}</span>
                    </div>
                      <div class="others">
                        <p v-if="ticket.Credential">{{ticket.Credential.CredentialName}} {{ticket.Credential.CredentialNo}}</p>
                        <P>{{$t('base.Seat')}} {{ticket.SeatTypeName}} {{ticket.SeatPosition}}</P>
                      </div>
                  </div>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false">
              <div class="cell-segment-one" :type="language">
                <div class="cell-segment-button" v-if="item.CanOperate">
                  <div class="button" @click="_gotoChange(item)">{{$t('base.Change')}}</div>
                  <div class="button" type="common" @click="_gotoRefund(item.TrainCode, item)">{{$t('base.Refund')}}</div>
                </div>
                <div v-else></div>
                <p @click="_getNoticeRules">{{$t('base.BookingInstructionsAndInstructionsForReturning')}}</p>
              </div>
            </hs-cell-1>
          </train-segment>
        </div>

        <!-- 其他信息 -->
        <hs-section>
          <hs-cell-small-title v-if="details.CustomItem && details.DisplayConfig"
            :title="details.DisplayConfig.CustomItemName"
            type="bold"
            :value="details.CustomItem"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="details.Purpose"
            :title="$t('base.TravelPurpose')"
            type="bold"
            :value="details.Purpose"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title
            :title="$t('base.BookingSource')"
            type="bold"
            :value="details.BookingSourceDesc"
            :showLogo="false">
          </hs-cell-small-title>
          <template v-if="language === 'CN'">
            <hs-cell-small-title v-if="details.VettingRecordInfos" @click.native="showReview = true"
              :title="$t('base.ApprovalInformation')"
              type="bold">
            </hs-cell-small-title>
          </template>
          <template v-else>
            <hs-cell-small-title v-if="details.VettingRecordInfos" @click.native="showReview = true"
              :title="$t('base.ApprovalInformation')"
              type="bold"
              :value="details.VettingStatusDesc">
            </hs-cell-small-title>
          </template>
          <hs-cell-small-title v-if="Number(details.TravelType) === 0"
            :title="$t('base.PaymentWay')"
            type="bold"
            :value="details.PayType | filterPayType"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="details.ReasonCode"
            :title="$t('base.ViolationOfRank')"
            type="bold"
            :value="details.ReasonCode"
            :warning="false"
            :showLogo="false"
            @header="_getTravelStandard">
          </hs-cell-small-title>
        </hs-section>
        <hs-section>
          <hs-cell-1  :showLogo="false" :showSeparator="false" v-for="(item, index) in details.Contacts" :key="index">
            <div class="custom-cell-people">
              <div  class="left" :style="{opacity: index === 0 ? 1 : 0}">
                <p>{{$t('base.ContactInformation')}}</p>
              </div>
              <div class="right" :style="{'border-bottom-width': index === details.Contacts.length - 1 ? '0px' : '1px'}">
                <div class="name">{{item.Name}}</div>
                <div class="others">
                  <p>{{$t('base.CellphoneNumber')}} {{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                  <p>{{$t('base.Email')}} {{item.Email ? item.Email : $t('base.Unfilled')}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
    </hs-scroll>
    <div class="footer" @click="showDiaLog">
      <div class="footer-price">
        <p>{{$t('base.TotalAmount')}}</p>
        <p>¥{{details.TotalPrice}}</p>
      </div>
      <div class="footer-price-details">
        <div class="logo" :style="{transform: showPriceDetail ? 'rotate(90deg)' : 'rotate(-90deg)'}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
      </div>
      <hs-dialog v-model="showPriceDetail" height="calc(100% - 50px)" ref="dialog">
        <hs-scroll class="showPriceDetail">
          <ul>
            <hs-cell-1 :showLogo="false"  v-for="(item, index) in goPrice" :key="`${index}goPrice`">
              <div class="footer-price-cell">
                <p>{{item.SeatTypeName}}<template v-if="JSON.stringify(backPrice) !== '[]'">({{$t('base.Going')}})</template></p>
                <p><span>¥ {{item.Price}} </span><span>x {{item.Amount}}</span></p>
              </div>
            </hs-cell-1>
            <template v-if="backPrice.length > 0">
              <hs-cell-1 :showLogo="false" v-for="(item, index) in backPrice" :key="`${index}backPrice`">
                <div class="footer-price-cell">
                  <p>{{item.SeatTypeName}}({{$t('base.Return')}})</p>
                  <p><span>¥ {{item.Price}} </span><span>x {{item.Amount}}</span></p>
                </div>
              </hs-cell-1>
            </template>
            <hs-cell-1 :showLogo="false">
              <div class="footer-price-cell">
                <p>{{$t('base.ServiceFee')}}</p>
                <p><span>¥ {{ServiceCharge}}</span></p>
              </div>
            </hs-cell-1>
          </ul>
        </hs-scroll>
      </hs-dialog>
    </div>
    <c-examine-process v-model="showReview" :datalist="details.VettingRecordInfos" :passengers="details.Passengers"></c-examine-process>
    <hs-alert :show.sync="showExamineDialog" :title="vettingContent === 1 ? $t('base.ApprovedPass') : $t('base.ApprovalRejection')" cancelBtn="" @confirm="_examineSubmit" maskClose>
      <div class="hs-alert-content" slot="content">
        <input maxlength='50' v-model="vettingMemo" :placeholder="$t('base.PleaseEnterAComment')"/>
      </div>
    </hs-alert>
    <!-- 时刻表 -->
    <c-table-time v-if="showTableTime" :params="currentTableTime"></c-table-time>
    <c-travel-standard v-model="showTravelStandard" :details="trainRight.TrainRank" :travelPolicy="trainRight.TravelPolicy" :type="3"></c-travel-standard>
    <pay ref="Pay"></pay>
    <hs-dialog v-model="showRulesDialog">
      <hs-scroll class="rulesDialog" :data="[rulesHtml]">
        <ul>
          <div v-html="rulesHtml"></div>
        </ul>
      </hs-scroll>
    </hs-dialog>
  </div>
</template>
<script type='text/ecmascript-6'>
import axios from 'axios'
import CHeader from 'components/c-header/c-header'
import HsTab from 'base/hs-tab/hs-tab'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCellSmallTitle from 'base/hs-tableview/hs-cell-small-title/hs-cell-small-title'
import HsNull from 'base/hs-null/hs-null'
import HsDialog from 'base/hs-dialog/hs-dialog'
import TrainSegment from 'pages/train/share/train-segment'
import HsButton from 'base/hs-button/hs-button'
import {kpiTrainOrderDetai, kpiTrainCancle, kpiVettingVettingHandleOrder, kpiTrainGetRefundTicketInitInfo} from 'api'
import CReturnExplain from 'components/c-return-explain/c-return-explain' // 退改签说明
import CExamineProcess from 'components/c-examine-process/c-examine-process'
import eventBus from 'common/js/eventBus.js'
import HsAlert from 'base/hs-alert/hs-alert'
import CTravelStandard from 'components/c-travel-standard/c-travel-standard'
import Pay from 'pages/pay/pay'
import Vue from 'vue'
import {mapGetters, mapMutations, mapActions} from 'vuex'
// import {kpiTrainGetChangeInitInfo} from 'api'
export default {
  created() {
    if (this.noValue(this.$route.query.Id)) {
      this.$router.replace('/planeTicket/orderList')
    } else if (!this.noValue(this.$route.query.Id)) {
      this._getOrderDetails()
    }
  },
  beforeRouteLeave (to, from, next) {
    if (this.showReview || this.showTableTime) {
      this.showReview = false
      this.showTableTime = false
      next(false)
    } else {
      if (to.name === 'TrainOrderInforList') {
        to.params.TravelType = Number(this.details.TravelType)
        this.showPriceDetail = false
      }
      next()
    }
    // if (to.name === 'TrainOrderInforList') {
    //   to.params.TravelType = Number(this.details.TravelType)
    //   this.showPriceDetail = false
    // } else {
    //   next()
    // }
  },
  activated() {
    this.details = {}
    eventBus.$off()
    eventBus.$on('refund', function () {
      this._getOrderDetails()
    }.bind(this))
    this._getOrderDetails()
  },
  computed: {
    ...mapGetters([
      'isMiniprogram',
      'trainRight',
      'trainOrder'
    ]),
    infoIndex() {
      return this.details.VettingRecordInfos ? this.details.VettingRecordInfos.map(v => v.TemplateNodeProcessStatusDesc).indexOf('进行中') : -1
    },
    goPrice() {
      return this.details.PriceDetails ? this.details.PriceDetails.filter(item => item.Type === 0) : []
    },
    backPrice() {
      return this.details.PriceDetails ? this.details.PriceDetails.filter(item => item.Type === 1) : []
    },
    ServiceCharge() {
      const serviceCharge = this.details.PriceDetails ? this.details.PriceDetails.reduce((sum, item) => {
        return sum + item.ServiceCharge * item.Amount
      }, 0) : 0
      console.log('ServiceCharge', serviceCharge)
      return serviceCharge
    }
  },
  data() {
    return {
      details: {},
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      showPriceDetail: false,
      showReview: false,
      vettingContent: 1,
      vettingMemo: '',
      showExamineDialog: false,
      showTableTime: false,
      currentTableTime: {},
      origin: {},
      isOrigin: '0',
      showTravelStandard: false,
      /** 退改须知和退改说明 */
      showRulesDialog: false,
      rulesHtml: ''
      /**************************/
    }
  },
  filters: {
    filterPayType(val) {
      switch (val) {
        case 1:
          return Vue.prototype.$t('base.CompanyPayment')
        case 2:
          return Vue.prototype.$t('base.PersonalPayment')
        case 3:
          return `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    },
    ticketStatus(val) {
      switch (val) {
        case 0:
          return Vue.prototype.$t('base.NotYetTicketed')
        case 1:
          return Vue.prototype.$t('base.Ticketing')
        case 2:
          return Vue.prototype.$t('base.TicketingFailed')
        case 3:
          return Vue.prototype.$t('base.Ticketed')
        case 4:
          return Vue.prototype.$t('base.ChangedRefund')
        case 5:
          return Vue.prototype.$t('base.Refunding')
        case 6:
          return Vue.prototype.$t('base.Refunded')
        case 7:
          return Vue.prototype.$t('base.Occupied')
        case 8:
          return Vue.prototype.$t('base.RefundFailed')
        case 9:
          return Vue.prototype.$t('base.ChangingTheStationThatArrived')
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  methods: {
    ...mapMutations([
      'SET_TRAIN_ORDER'
    ]),
    ...mapActions([
      'dispatchTrainRight'
    ]),
    _getOrderDetails() {
      let params = {
        orderID: this.$route.query.Id
      }
      kpiTrainOrderDetai(params).then((res) => {
        console.log('火车票详情', res)
        this.details = res.ResultData
        this.origin = res.ResultData.OriginRoute ? res.ResultData.OriginRoute : {}
        this.isOrigin = res.ResultData.OriginRoute ? '1' : '0'
        // console.log('改签', JSON.stringify(this.origin))
        // console.log('配送方式', this.details.DeliveryType)
      })
    },
    _getCancle() {
      this.$alert({
        content: this.$t('TM.AreYouSureToCancelTheOrder'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = {
            orderID: this.$route.query.Id
          }
          kpiTrainCancle(params).then((res) => {
            if (res.ResultData === true) {
              this.$toast(this.$t('TM.OrderCancelled'))
              setTimeout(() => { eventBus.$emit(`refresh`) }, 0)
              this.$router.replace({name: 'TrainOrderInforList', params: {TravelType: Number(this.details.TravelType)}})
            } else {
              this.$toast(this.$t('TM.OrderCancelFailed'))
            }
          })
        }
      })
    },
    showDiaLog() {
      if (this.showPriceDetail) {
        this.showPriceDetail = false
      } else {
        this.showPriceDetail = true
      }
    },
    _clickGetOrder() {
      this.$router.push({path: '/train/book', query: {isExamine: true}})
    },
    _showTipDialog(tipContent, next, cancel, confirmBtn = this.$t('base.Confirm'), cancelBtn = this.$t('base.Cancel')) {
      this.dialog = this.$createDialog({
        type: 'confirm',
        icon: 'cubeic-alert',
        title: this.$t('base.Tips'),
        content: tipContent,
        confirmBtn: {
          text: confirmBtn,
          active: true,
          disabled: false,
          href: 'javascript:;'
        },
        cancelBtn: {
          text: cancelBtn,
          active: false,
          disabled: false,
          href: 'javascript:;'
        },
        onConfirm: next,
        onCancel: cancel
      })
      this.dialog.show()
    },

    /** 去改签 */
    _gotoChange(item) {
      let res = JSON.parse(JSON.stringify(this.trainOrder))
      res.DepartObject = {
        CityName_CN: item.DepartStationName,
        CityName_EN: item.DepartStationName,
        CityCode: item.DepartStationCode
      }
      res.ArrivalObject = {
        CityName_CN: item.ArrivalStationName,
        CityName_EN: item.ArrivalStationName,
        CityCode: item.ArrivalStationCode
      }
      res.DepartStationCode = item.DepartStationCode
      res.ArrivalStationCode = item.ArrivalStationCode
      res.DepartStationName = item.DepartStationName
      res.ArrivalStationName = item.ArrivalStationName
      res.DepartDate = this.beTime(new Date())
      res.ArrivalDate = ''
      res.OriginalOrderID = this.$route.query.Id
      this.SET_TRAIN_ORDER(res)
      this.$router.push({
        path: '/train/trainQueryList',
        query: {
          originalDetails: JSON.stringify(item),
          isOneWay: true
        }
      })
    },

    /** 去退票 */
    _gotoRefund(TrainCode, item) {
      let params = {
        TrainCode: TrainCode,
        OrderID: this.details.OrderID
      }
      kpiTrainGetRefundTicketInitInfo(params).then((res) => {
        console.log('退票详情', res)
        let details = res.ResultData
        if (details.Route.Tickets) {
          details.Route.Tickets = details.Route.Tickets.filter(item => item.IssueStatus === 3).map(v => {
            v.select = false
            return v
          })
        }
        if (details.Route.Tickets && details.Route.Tickets.length > 0) {
          this.$router.push({
            path: '/train/refund',
            // query: {Id: 1, TrainCode: TrainCode, OrderID: this.details.OrderID, Detail: res, isExamine: true}})
            query: {
              OrderID: this.details.OrderID,
              details: JSON.stringify(details)
            }
          })
        } else {
          this.$toast(this.$t('TM.PassengersWithoutRefundableTickets'))
        }
      })
    },

    // 审批
    _examineSubmit() {
      let params = {
        ID: this.$route.query.Id,
        InstanceID: this.details.VettingRecordInfos[this.infoIndex].InstanceID,
        TemplateNodeID: this.details.VettingRecordInfos[this.infoIndex].TemplateNodeID,
        VettingContent: this.vettingContent,
        VettingMemo: this.vettingMemo,
        BusinessType: 9 // 火车票
      }
      kpiVettingVettingHandleOrder(params).then((res) => {
        console.log('审批: ', res)
        if (res.ResultData === true) {
          this.$toast(this.$t('TM.ApprovalSubmitted'), {
            complete: () => {
              setTimeout(() => { eventBus.$emit(`refresh`) }, 0)
              this.$router.replace({path: '/message/examine', query: {QuerySource: 0}})
            }
          })
        }
      })
    },
    /** 支付 */
    _gotoPay() {
      this.$refs.Pay && this.$refs.Pay.gotoPay({
        OrderBusinessType: 10,
        OrderID: this.$route.query.Id,
        PayWay: 3,
        TravelType: Number(this.details.TravelType),
        CallbackPathName: 'TrainOrderInforList'
      })
    },
    // 获取差旅职级
    _getTravelStandard() {
      this.dispatchTrainRight().then((res) => {
        console.log('trainRight: ', this.trainRight)
        this.showTravelStandard = true
      })
    },
    // 根据航段显示单程/往返程/N程
    _showSegmentTitle(index) {
      let length = this.details.Routes && !this.noValue(JSON.stringify(this.details.Routes)) ? this.details.Routes.length : 0
      if (length === 1) {
        return this.$t('base.OneWay')
      } else if (length === 2) {
        if (this.details.Routes[0].DepartStationName === this.details.Routes[1].ArrivalStationName) {
          return index === 0 ? this.$t('base.Going') : this.$t('base.Return')
        } else {
          return this.$t('base.NoXSegment', 'function', index + 1)
        }
      } else {
        return this.$t('base.NoXSegment', 'function', index + 1)
      }
    },
    /** 预订须知和退改说明 */
    _getNoticeRules() {
      let url = '/static/html/train_notice.html'
      axios.get(url).then((res) => {
        this.rulesHtml = res.data
        this.showRulesDialog = true
      })
    }
  },
  components: {
    CHeader,
    HsTab,
    HsScroll,
    HsSection,
    HsCell1,
    HsCellSmallTitle,
    HsNull,
    HsDialog,
    TrainSegment,
    HsButton,
    CReturnExplain,
    CExamineProcess,
    eventBus,
    HsAlert,
    CTravelStandard,
    Pay,
    CTableTime: () => import('components/c-table-time/c-table-time')
  }
}
</script>
<style lang='stylus' scoped rel='stylesheet/stylus'>
