@import '~common/stylus/variable'
.hs-container
     width 100%
     .table-view
      width 100%
      height calc(100% - 95px)
      overflow hidden
     .cell-top
        width 100%
        padding 10px 0px
        @extend .flex-column
        >.cell-top-one
          @extend .flex-row
          @extend .flex-between
          font-size 1rem
          margin-bottom 8px
          >p
            &:last-child
             visibility hidden
        >.cell-top-one[type="EN"]
          font-size 0.8rem
          >p
            &:nth-child(3)
              display none
        >.cell-top-two
          @extend .flex-column
          @extend .flex-start
          @extend .flex-top
          line-height 1.2em
          width calc(100% - 80px)
          @extend .flex-column
          >p
            &:nth-child(1)
              font-size 0.8 rem
              color $color_gray_text
            &:nth-child(2)
              font-size 0.8 rem
              color $color_gray_text
        .cell-button
          @extend .flex-row
          width 100%
          >.button
            flex 1
            margin 10px 5px 0 5px
            &:nth-child(1)
              margin-left 0px
            &:nth-child(2)
              margin-right 0px
    .cell-segment-one
      @extend .flex-row
      @extend .flex-between
      width 100%
      .cell-segment-button
        @extend .flex-row
        @extend .flex-between
        >.button
          @extend .flex-center
          font-size 0.9rem
          color $color_theme
          border 1px solid $color_theme
          border-radius 11px
          margin-right 15px
          width 50px
          height 22px
          &:last-child
            margin-right 0px
      >p
        font-size 0.8rem
        color $color_black_text_1
    .cell-segment-one[type="EN"]
      .cell-segment-button
        @extend .flex-column
        @extend .flex-center
        margin 15px 0
        >.button
          width 120px
          font-size 0.8rem
          margin-right 0px
          margin-bottom 10px
          &:last-child
            margin-bottom 0px
      >p
        width calc(100% - 130px)
    .custom-cell-people
      @extend .flex-between
      @extend .flex-start
      @extend .flex-top
      width 100%
      >.left
        width 90px
        padding 15px 0
        >p
          @extend .flex-row
          @extend .flex-start
          font-size 1rem
          color $color_black_text_1
          line-height 1.4em
      >.right
        @extend .flex-column
        width calc(100% - 100px)
        padding 15px 0
        border-bottom 1px dashed $color_gray_layer
        >.name
          @extend .flex-row
          @extend .flex-between
          font-size 1rem
          line-height 1.4em
          >p
            font-weight bold
          >span
            color $color_theme
            font-size 0.8rem
        >.others
          width 100%
          >p
            line-height 1.5em
            color $color_gray_text
            font-size 1rem
            &:first-child
              margin-top 7px
    .custom-line
      border-top 1px solid $color_gray_layer
    .cell-passenger
      width 100%
      >.cell-passenger-top
        @extend .flex-row
        @extend .flex-between
        >p
          &:nth-child(1)
            font-size 1 rem
            color $color_black_text_1
    .cell-passenger-list
      @extend .flex-row
      width 100%
      padding 10px 0
      color $color_black_text_1
      line-height 1.2em
      .cell-passenger-left
        width calc(100% - 150px)
        @extend .flex-column
        >p
          &:nth-child(1)
            font-size 1 rem
            font-weight bold
          &:nth-child(2)
            font-size 0.8rem
          &:nth-child(3)
            font-size 0.8rem
      .cell-passenger-right
        width 150px
        @extend .flex-column
        .cell-passenger-rightTop
          @extend .flex-row
          @extend .flex-between
          >p
            font-size 0.8 rem
            &:nth-child(1)
              margin-left 25px
            &:nth-child(2)
              color $color_theme
        .cell-passenger-rightBottom
          @extend .flex-row
          @extend .flex-end
          >p
            &:nth-child(1)
              font-size 0.8 rem
              margin-left 10px
            &:nth-child(2)
              font-size 0.8rem
    .cell-contact-left
      @extend .flex-column
      line-height 1.2em
      margin 10px 0px
      >p
       &:nth-child(1)
        font-weight bold
       &:nth-child(2)
        font-size 0.8 rem
       &:nth-child(3)
        font-size 0.8 rem
    .cell-share
      padding 10px 0px
      font-weight bold
      word-wrap break-word
      word-break break-all
      overflow hidden
    .cell-3-title
      @extend .flex-row
      @extend .flex-start
      width 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        margin-left 3px
    .footer
      width 100%
      height 50px
      padding 0 15px
      background-color $color_white
      border-top 1px solid $color_gray_layer
      @extend .flex-row
      @extend .flex-between
      >.footer-price
        @extend .flex-row
        @extend .flex-start
        width calc(100% - 30px)
        height 100%
        >p
          &:nth-child(1)
            font-size 1 rem
            color $color_gray_text
          &:nth-child(2)
            margin-left 10px
            color $color_black_text_1
            font-size 1 rem
            color $color_theme
            font-weight bold
      >.footer-price-details
        @extend .flex-row
        @extend .flex-between
        width 20px
        height 100%
        >.p
          &:nth-child(1)
            color $color_black_text_1
            font-size 0.9rem
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
          transition all 300ms
      .showPriceDetail
         position absolute !important
         background-color $color_white
         width 100%
         bottom 0
         max-height 70%
        .foot-price-head
          color $color_black_text_0
          font-size 0.8 rem
        .footer-price-cell
          @extend .flex-row
          @extend .flex-between
          width 100%
          >p
            color $color_black_text_0
            font-size 0.8 rem
            >span
              &:nth-child(1)
                color $color_theme

    .hs-alert-content
      width 100%
      padding 0 20px
      >input
        width 100%
        height 44px
        border 1px dashed $color_gray_layer
        border-radius 2px
        font-size 0.9rem
        text-align center
</style>



